.headerbar{
    width: 75%;
    margin: auto;
    height: 100px;
    font-family : 'Georama', sans-serif
    
}

.headermenu{
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

img{
    width: 150px;
}

.menuicon{
    margin: 0 20px;
    text-decoration: none;
    background-color: #118aef;
    color: white;
    padding: 10px 20px;
    border-radius: 5px;
    border: 1px solid cadetblue;
}

.menuicon:hover{
    background-color: cadetblue;
    color: white;
    padding: 10px 20px;
    margin: 0 20px;
    border-radius: 5px;
    border: 1px solid cadetblue;
    transition: .7s ease-in-out;
}


.headermenu input{
    padding: 8px;
    width: 400px;
    border: 2px solid #118aef;
    border-radius: 5px;
    color: #118aef;
}

.searchbtn{
    background-color: blueviolet;
    color: white;
    margin: 0 10px;
    padding: 8px 20px;
    border: 2px solid blueviolet;
    text-decoration: none;
    border-radius: 15px;
}

.searchbtn:hover{
    background-color: cadetblue;
    color: white;
    border: 2px solid cadetblue;
    transition: .3s ease-in-out;
}

.loginbtn{
    
    color: brown;
    margin-left:auto ;
    
    padding: 8px 30px;
    border: 2px solid brown;
    text-decoration: none;
    border-radius: 5px;
}

.loginbtn:hover{
    
    color: white;
    margin-left:auto ;
    background-color: brown;
    padding: 8px 30px;
    border: 2px solid brown;
    text-decoration: none;
    border-radius: 5px;
    transition: .8s ease-in-out;
}

input:focus{
    outline: none;
}

