.footerbg{
    background-color: #1A1A1A;
    color: white;
}

.footer{
    width: 75%;
    margin: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 30px;
    
}


.col{
    /* margin: 5px; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.col a{
    text-decoration: none;
    color: teal;
    margin: 5px 0;
    padding: 6px;
}

.app {
    padding: 5px 10px;
    margin: 5px;
    border: 1px solid white;
}

.app a:hover{
    padding: 7px 9px;
    margin: 6px 0;
    border: 1px solid white;
}

.copy{
    display: flex;
    justify-content: center;
    align-items: center;
    /* padding: 20px 0; */
}

.col a:hover{
    border-bottom: 1px solid white;
    padding-top: 5px;
}

