*{
    margin: 0%;
    padding: 0;
}

.topmenu{
    background-color: rgb(230, 236, 239);
    font-family: 'Georama', sans-serif;
    
}

.menu{
    width: 75%;
    margin: auto;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    height: 40px;
}

.menu a{
    text-decoration: none;
    margin: 0 0 0 20px;
    color: brown;
}

.menu a:hover{
    color: blueviolet;
    transition: 0.1s ease-in-out;
}